import React from 'react';
import Heading from '@/components/heading';
import styles from './index.less';

import { advantage } from 'data/home';

function Item({ icon, title, des }) {
  return (
    <li>
      <div className={styles.content}>
        {icon}
        <h3 className={styles.title}>{title}</h3>
        <p className={styles.des}>{des}</p>
      </div>
    </li>
  );
}

export default function Advantages() {
  return (
    <div className={styles.Advantages}>
      <div className="center-block">
        <div className="block-left">
          <Heading title={advantage.title} des={advantage.desc} en={<>Our advantages</>} />
        </div>
        <ul className={`block-right ${styles.list}`}>
          {advantage.dataSource.map((dataSource, index) => {
            return (
              <Item
                key={index}
                icon={
                  <span
                    className={styles.icon}
                    style={{ backgroundPosition: dataSource.iconPosition }}
                  />
                }
                title={dataSource.title}
                des={dataSource.desc}
              />
            );
          })}
        </ul>
      </div>
    </div>
  );
}
