import React from 'react';
import Heading from '@/components/heading';

import { ability } from 'data/home';
import styles from './index.less';

export default function Ability() {
  const getList = (title, list) => {
    return (
      <div className={styles.listWrap}>
        <div className={styles.title}>{title}</div>
        <img src={require('@/static/images/ico01.png')} className={styles.bgIcon} alt="" />
        <ul className={styles.list}>
          {list.map(text => {
            return (
              <li key={text} className={styles.item}>
                <i className="iconfont icontick" />
                {text}
              </li>
            );
          })}
        </ul>
      </div>
    );
  };
  return (
    <div className={styles.Ability}>
      <div className="center-block">
        <div className="block-left">
          <Heading
            title={ability.title}
            des={ability.desc}
            en={<span>PCBA manufacturing capacity</span>}
          />
        </div>
        <div className="block-right">
          {ability.dataSource.map((dataSource, index) => {
            return (
              <div key={index} className={styles.line}>
                <div className={styles.imageContainer}>
                  <img
                    className={styles.matchImg}
                    style={{ marginLeft: 2 }}
                    src={dataSource.icon}
                    alt={dataSource.title}
                    width="550"
                    height="350"
                  />
                </div>
                {getList(dataSource.title, dataSource.descs)}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
