import React, { useState } from 'react';
import Head from 'next/head';
import axios from 'axios';
import { notify } from 'react-notify-toast';
import RequestServiceSuccessModal from '@/components/requestServiceSuccessModal';
import PageFooter from '@/components/pageFooter';
import Contact from '@/components/Contact';
import Solutions from './components/Solutions';
import Ability from './components/Ability';
import Advantages from './components/Advantages';
import Business from './components/Business';
import Partner from './components/Partner';

import global from 'data/global';
import home from 'data/home';

import styles from './index.less';

export default function Index() {
  const [phone, setPhone] = useState('');
  const [visible, setVisible] = useState(false);

  const refPhone = React.createRef();

  const onSubmit = () => {
    if (!phone.trim()) {
      notify.show('请输入您的电话号码', 'error');
      refPhone.current.focus();
      return;
    }

    axios.post('/api/common/leave/phone', { phone: phone.trim() }).then(
      result => {
        setVisible(true);
        setPhone('');
      },
      error => {
        notify.show('服务器内部错误，请稍后重试', 'error');
        console.log(error);
      },
    );
  };

  return (
    <>
      <Head>
        <title>
          {global.companyName}-{global.companyDesc}
        </title>
        <meta name="description" content={global.companyDescription} />
        <meta name="keyword" content={global.companyKeywords} />
      </Head>

      <div className={styles.topBanner}>
        <img className={styles.slogan} src={require('@/static/images/slogan.png')} alt="" />
        <div className={styles.mission}>{home.title}</div>

        {/* <div className={styles.callForm}>
          <input
            placeholder="请输入您的电话号码"
            type="text"
            maxLength="11"
            ref={refPhone}
            className={styles.input}
            value={phone}
            onChange={e => {
              setPhone(e.target.value);
            }}
            onKeyPress={e => {
              if (e.key === 'Enter') {
                onSubmit();
              }
            }}
          />
          <button type="button" className={styles.button} onClick={onSubmit}>
            立即预约
          </button>
        </div>

        <div className={styles.callTip}>现在预约，获取报价</div> */}
      </div>

      <div className={styles.effects}>
        <div className={styles.center}>
          {global.types.map((type, index) => {
            return (
              <div key={index} className={styles.effect}>
                {type}
              </div>
            );
          })}
        </div>
      </div>

      <div className={styles.blocks}>
        <Solutions />
        <Business />
        <Advantages />
        <Ability />
        <Partner />
        <Contact />
        <PageFooter />
      </div>

      <RequestServiceSuccessModal
        visible={visible}
        onClose={() => {
          setVisible(false);
        }}
      />
    </>
  );
}
